import React from 'react'
import Pet from "./Pet/Pet"
import {PetProps} from "../../api/PetsApi"
import classes from "./Pets.module.css"

const Pets = (props: {pets: PetProps[]}) => (
        <div className={classes.Pets}>
        {props.pets.map(pet => 
                        <Pet 
                        key={pet.id}
                        pet={pet} />
                )} 
        </div>
)

export default Pets